<template>
    <div>
        <label :for="id"> {{ label }} </label>
        <v-text-field :id="id" v-bind="{ ...$attrs, ...commonAttrs }" v-on="$listeners">
            <template v-for="(_, scopedSlotName) in $scopedSlots" #[scopedSlotName]="slotData">
                <slot :name="scopedSlotName" v-bind="slotData" />
            </template>
            <template v-for="(_, slotName) in $slots" #[slotName]>
                <slot :name="slotName" />
            </template>
        </v-text-field>
    </div>
</template>

<script>
export default {
    name: 'InputField',
    inheritAttrs: false,
    props: {
        id: {
            type: String,
            default: null
        },
        label: {
            type: String,
            default: null
        }
    },
    computed: {
        commonAttrs() {
            return {
                label: null,
                persistentHint: true,
                outlined: true,
                dense: true,
                hideDetails: false,
                backgroundColor: this.$vuetify['theme'].isDark ? 'black' : 'white',
                class: {
                    'mt-2': this.$props['label']
                }
            }
        },
    },
}
</script>
